import { Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Facebook as FacebookIcon, LinkedIn as LinkedInIcon } from '@material-ui/icons';
import React from 'react';

import { MY } from '../../../constants';

const useStyles = makeStyles((theme) => ({
  link: {
    color: theme.palette.text.secondary,
  },
  icon: {
    fontSize: 40,
  },
}));

export const Social = () => {
  const classes = useStyles();

  const socials = [
    {
      href: MY.URL.FACEBOOK,
      icon: <FacebookIcon className={classes.icon} />,
    },
    {
      href: MY.URL.LINKED_IN,
      icon: <LinkedInIcon className={classes.icon} />,
    },
  ];

  return (
    <Box>
      {socials.map((social) => (
        <a key={social.href} className={classes.link} href={social.href} target="blank">
          {social.icon}
        </a>
      ))}
    </Box>
  );
};
